import React from 'react';

import { PasswordInput } from '../../../../index';


const PasswordInputSection = (props) => (
  <div>
    <h1><a href="http://pearson-higher-ed.github.io/design/c/inputs/beta/#specialized-inputs">Password Input</a></h1>

      <div className="code">
        <h2>Props:</h2>
        <p>PasswordInput:</p>
        <ul>
          <li>id:String           === "a unique name"</li>
          <li>fancy:Boolean       === true/false</li>
          <li>labelText:String    === "a desciptive label"</li>
          <li>placeholder:String  === "a placeholder text"</li>
          <li>inputType:String    === "default", "error", "disabled", "readOnly"</li>
          <li>showText:String     === "show/hide text"</li>
          <li>hideText:String     === "show/hide text"</li>
          <li>error:Boolean       === true, false</li>
          <li>infoMessage:String  === "an optional info message displayed below the input"</li>
          <li>errorMessage:String === "an optional error message displayed below the input"</li>
        </ul>
      </div>

      <h2>PasswordInput - Fancy:</h2>
      <PasswordInput
        id           = "password1"
        fancy        = {true}
        labelText    = {props.intlDefaultText.passwordLabel}
        placeholder  = {props.intlDefaultText.passwordPlaceholder}
        showText     = {props.intlDefaultText.showText}
        hideText     = {props.intlDefaultText.hideText}
        infoMessage  = {props.intlDefaultText.textInputInfoMessage}
        errorMessage = {props.intlDefaultText.textInputErrorMessage}
        />
      <p className="code">{'<PasswordInput id="password1" fancy={true} labelText={intlDefaultText.passwordLabel} placeholder={intlDefaultText.passwordPlaceholder} showText={intlDefaultText.showText} hideText={intlDefaultText.hideText} infoMessage={props.intlDefaultText.textInputInfoMessage} errorMessage={props.intlDefaultText.textInputErrorMessage} />'}</p>

      <br />
      <br />


      <h2>PasswordInput - Basic:</h2>
      <PasswordInput
        id           = "password2"
        labelText    = {props.intlDefaultText.passwordLabel}
        placeholder  = {props.intlDefaultText.passwordPlaceholder}
        showText     = {props.intlDefaultText.showText}
        hideText     = {props.intlDefaultText.hideText}
        infoMessage  = {props.intlDefaultText.textInputInfoMessage}
        errorMessage = {props.intlDefaultText.textInputErrorMessage}
        />
      <p className="code">{'<PasswordInput id="password2" labelText={intlDefaultText.passwordLabel} placeholder={intlDefaultText.passwordPlaceholder} showText={intlDefaultText.showText} hideText={intlDefaultText.hideText} infoMessage={props.intlDefaultText.textInputInfoMessage} errorMessage={props.intlDefaultText.textInputErrorMessage} />'}</p>


      <br />
      <br />


      <h2>PasswordInput - Basic Error:</h2>
      <PasswordInput
        id           = "password3"
        error        = {true}
        inputType    = "error"
        labelText    = {props.intlDefaultText.passwordLabel}
        placeholder  = {props.intlDefaultText.passwordPlaceholder}
        showText     = {props.intlDefaultText.showText}
        hideText     = {props.intlDefaultText.hideText}
        infoMessage  = {props.intlDefaultText.textInputInfoMessage}
        errorMessage = {props.intlDefaultText.textInputErrorMessage}
        />
      <p className="code">{'<PasswordInput id="password3" labelText={intlDefaultText.passwordLabel} placeholder={intlDefaultText.passwordPlaceholder} error={true} showText={intlDefaultText.showText} hideText={intlDefaultText.hideText} infoMessage={props.intlDefaultText.textInputInfoMessage} errorMessage={props.intlDefaultText.textInputErrorMessage} />'}</p>


      <br />
      <br />


      <h2>PasswordInput - Fancy Error:</h2>
      <PasswordInput
        id           = "password4"
        fancy        = {true}
        inputType    = "error"
        labelText    = {props.intlDefaultText.passwordLabel}
        placeholder  = {props.intlDefaultText.passwordPlaceholder}
        showText     = {props.intlDefaultText.showText}
        hideText     = {props.intlDefaultText.hideText}
        infoMessage  = {props.intlDefaultText.textInputInfoMessage}
        errorMessage = {props.intlDefaultText.textInputErrorMessage}
        />
      <p className="code">{'<PasswordInput id="password4" fancy={true} labelText={intlDefaultText.passwordLabel} placeholder={intlDefaultText.passwordPlaceholder} error={true} showText={intlDefaultText.showText} hideText={intlDefaultText.hideText} infoMessage={props.intlDefaultText.textInputInfoMessage} errorMessage={props.intlDefaultText.textInputErrorMessage} />'}</p>

  </div>
)

export default PasswordInputSection;



